
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        


































































.yeti-article {
  max-width: 1039px;
  margin-top: 1.75em;
}
::v-deep {
  h3 {
    margin-top: 2em;
  }
  p {
    margin: 1em 0;
  }
  ul {
    list-style-type: disc;
  }
  figure img {
    max-width: 90%;
    max-height: 500px;
  }
}
.yeti-article--disclaimer ::v-deep li {
  margin: 2em 0;
}
