@import '@/assets/sass/variables.scss';

.inputs-bra {
  margin-left: 2rem;
  position: relative;

  .input-bra-high {
    position: absolute;
    left: 39px;
    top: 62px;
  }

  .input-bra-threshold {
    position: absolute;
    left: 115px;
    top: 40px;
    width: 85px;
  }

  .input-bra-low {
    position: absolute;
    left: 39px;
    top: 78px;
  }
}

.inputs-bra-different {
  .input-bra-high {
    top: 45px;
  }
}

.yetimountains {
  border: 1px solid #dbdbdb;
  border-radius: 4px;

  &:hover {
    border-color: #b5b5b5;
  }

  &-title {
    cursor: pointer;
    padding: 0.25rem 0.75rem;
  }

  &-arrow {
    color: $primary;
    margin-top: 0.25rem;
  }

  &-list {
    columns: 3 170px;
    padding: 0.75rem 2rem;
  }

  &-listtitle {
    font-weight: bold;
    padding: 0 0.75rem;
  }
}

.yetimountains-listelement + .yetimountains-listtitle {
  margin-top: 0.5rem;
}
