@import '@/assets/sass/variables.scss';

$section-padding: 1.5rem; //TODO find this variable
$box-padding: 1.25rem; //TODO find this variable
$header-height: 34px;
$box-margin: 1.5rem; //TODO find this variable
$yeti-height: calc(
  100vh - #{$navbar-height} - #{$section-padding} - 2 *#{$box-padding} - #{$header-height} - #{$box-margin} - #{$box-margin}
);

.map-container {
  position: relative;

  .legend {
    position: absolute;
    z-index: 6;
    top: 1.25rem;
    right: 1.25rem;

    .legend-button {
      position: static;

      button {
        width: auto;
        padding: 0 0.5em;
      }
    }

    .legend-content {
      margin-top: 0.5rem;
      margin-left: 1.25rem;
      border-radius: 2px;
      border: 1px solid lightgray;
      padding: 0.5rem;
      background: white;
      clear: both;
    }

    .legend-color {
      vertical-align: bottom;
      display: inline-block;
      width: 21px;
      height: 21px;
      margin-right: 5px;
    }
  }

  .opacity {
    position: absolute;
    z-index: 5;
    top: 3.5rem;
    right: 1.25rem;

    .opacity-slider {
      font-size: 1.14em;
      margin: 1px;
      width: 1.375em;
      padding: 1rem 0;
      background: rgba(0, 60, 136, 0.5);
      border-radius: 2px;

      &:hover {
        background: rgba(0, 60, 136, 0.7);
      }
    }

    .vue-slider {
      padding: 0 9px !important;
      height: 300px !important;
      max-height: 30vh;
    }

    .vue-slider-process {
      background: $white;
    }

    .vue-slider-rail {
      background: $black;
    }
  }
}
@media screen and (max-width: $tablet) {
  .map-container {
    height: $yeti-height;
    padding-left: 0;
    padding-top: 0;
    padding-bottom: 0;

    .legend {
      top: 0.5rem;

      .legend-content {
        margin-left: 0.5rem;
      }
    }

    .opacity {
      top: 2.75rem;
    }
  }
}

@media screen and (min-width: $tablet) {
  .yeti-content {
    .map-container {
      min-height: 100%;
    }
  }
}
